import {
  ButtonPanorama,
  Container,
  HeaderPanorama,
  Icon360,
  PanoramaContainer,
  PanoramaContent,
  TypographyDescription,
  TypographyH1
} from '../../styles/panorama';
import { useState } from 'react';
import { ThreeScene } from '../../webgl/ThreeScene';

export const Panorama = () => {
  const [isActive, setIsActive] = useState<boolean>(false);

  const handleButtonClick = () => {
    setIsActive(true);
  };

  return (
    <Container>
      <HeaderPanorama>
        <TypographyH1>  </TypographyH1>
        <Icon360>360</Icon360>
      </HeaderPanorama>
      <TypographyDescription>
               <br />       ?
      </TypographyDescription>
      <PanoramaContainer>
        <a id="panorama"></a>
        <PanoramaContent isActive={isActive}>
          <ThreeScene initialPanoramaId={1} isInteractive={isActive} />
        </PanoramaContent>
        {!isActive && <ButtonPanorama onClick={handleButtonClick}> </ButtonPanorama>}
      </PanoramaContainer>
    </Container>
  );
};
